<template>
  <!-- 分类页面 -->
  <div class="classify">
    <!-- 导航条 -->
    <van-nav-bar title="菜谱分类" fixed @click-left="back" left-arrow>
      <template #right>
        <van-icon @click="goSearch" name="search" size="18" />
      </template>
    </van-nav-bar>
    <div class="content">
      <div class="aaa">
        <van-sidebar v-model="activeKey" class="sidebar">
          <van-sidebar-item
            v-for="(item, index) in sidebar"
            :key="index"
            :title="item.title"
            :to="{ name: item.routerName }"
          />
        </van-sidebar>
        <router-view class="remen"></router-view>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'Classify',
  data() {
    return {
      activeKey: 0,
      sidebar: [
        {
          title: '热门',
          routerName: 'Remen',
        },
        {
          title: '菜式',
          routerName: 'Caishi',
        },
        {
          title: '肉菜',
          routerName: 'Roucai',
        },
        {
          title: '素菜',
          routerName: 'Sucai',
        },
        {
          title: '主食',
          routerName: 'Zhushi',
        },
        {
          title: '菜系',
          routerName: 'Caixi',
        },
        {
          title: '场景',
          routerName: 'Changjing',
        },
        {
          title: '网红美食',
          routerName: 'Wanghongmeishi',
        },
        {
          title: '人群',
          routerName: 'Renqun',
        },
        {
          title: '食疗养生',
          routerName: 'Shiliaoyangsheng',
        },
        {
          title: '其他',
          routerName: 'Qita',
        },
        {
          title: '人群',
          routerName: 'Renqun',
        },
        {
          title: '食疗养生',
          routerName: 'Shiliaoyangsheng',
        },
        {
          title: '其他',
          routerName: 'Qita',
        }
      ],
    }
  },
  created() {
    let routeName = this.$route.name
    for (let i = 0; i < this.sidebar.length; i++) {
      if (this.sidebar[i].routerName === routeName) {
        this.activeKey = i
      }
    }
  },
  methods: {
    // 返回
    back() {
      this.$router.push({ name: 'Home' })
    },

    goSearch() {
      this.$router.push({ name: 'Search' })
    },
  },
}
</script>

<style lang="less" scoped>
/deep/ .van-nav-bar__title {
  margin-left: 60px;
  font-weight: bold;
  font-size: 20px;
}

.sidebar{
  position: fixed;
    overflow-y: scroll;
    height: 600px;
    top: 50px;
}

::-webkit-scrollbar {
  /*滚动条整体样式*/
  width: 0px; /*高宽分别对应横竖滚动条的尺寸*/
  height: 0px;
}
::-webkit-scrollbar-thumb {
  /*滚动条里面小方块*/
  border-radius: 10px;
  box-shadow: inset 0 0 5px rgba(97, 184, 179, 0.1);
  background: #fff;
}
::-webkit-scrollbar-track {
  /*滚动条里面轨道*/
  box-shadow: inset 0 0 5px rgba(87, 175, 187, 0.1);
  border-radius: 10px;
  background: #ededed;
}


.classify {
  .content {
    padding-top: 50px;
    display: flex;
    .sidebar {
      width: 80px;
    }
    .remen {
      flex: 1;
    }
  }
}
/deep/ .van-sidebar-item--select::before {
  position: absolute;
  top: 50%;
  left: 0;
  width: 0.10667rem;
  height: 0.42667rem;
  background-color: #facc30;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
  content: '';
}
</style>
